let mapBlock = document.getElementById("map")
let mapBlock2 = document.getElementById("map2")

if(mapBlock){
    ymaps.ready(init);
}
if(mapBlock2){
    ymaps.ready(init2);
}

function init() {
    let coord = [55.673392, 37.632475]
    let mapPoints = [
        {
            title: "г. Москва, Каширское шоссе дом 3 корпус 2, строение 2",
            workTime: `Понедельник-воскресенье: <br>8:00–21:00`,
            cords: [55.673392, 37.632475]
        },
    ]
    let myMap = new ymaps.Map("map", {
        center: coord,
        zoom: 16,
        controls: []
    });



    mapPoints.forEach(el => {
        let placeMark = new ymaps.Placemark(el.cords, {
                balloonContentHeader: ``,
                balloonContentBody: `
                <div class="place-card">
                    <div class="place-card__inner">
                        <div class="text16">
                            ${el.title}
                        </div>
                        <div class="text16">
                            ${el.workTime}
                        </div>
                    </div>
                </div>
                `,
                balloonContentFooter: '',
                hintContent: '',
            },
            {
                iconLayout: 'default#image',
                iconImageHref: "img/mark.svg",
                iconImageSize: [56, 79],
            }
        );
        myMap.geoObjects.add(placeMark);
    })
}

function init2() {
    let coord = [55.673392, 37.632475]
    let mapPoints = [
        {
            cords: [55.673392, 37.632475]
        },
        {
            cords: [55.671, 37.632475]
        },
        {
            cords: [55.671, 37.635]
        },
    ]
    let myMap = new ymaps.Map("map2", {
        center: coord,
        zoom: 16,
        controls: []
    });



    mapPoints.forEach(el => {
        let placeMark = new ymaps.Placemark(el.cords, {},
            {
                iconLayout: 'default#image',
                iconImageHref: "img/mark.svg",
                iconImageSize: [32, 46],
            }
        );
        myMap.geoObjects.add(placeMark);
    })
}
