$(".tab").click(function() {
    let parentBlock = $(this).parents(".tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find(".tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.tab-${tabId}`).addClass("_active")

    setActiveTabSelect($(this).parents(".tabs-parent"))
    $(".tabs__select._active").removeClass("_active")
})

$(".tabs__select").each(function (index,select) {
    setActiveTabSelect($(select).parents(".tabs-parent"))
})

function setActiveTabSelect(parent) {
    $(parent).find(".tabs__current").html($(parent).find(".tab._active span").text())
}

$(".tabs__select").click(function () {
    $(this).toggleClass("_active")
})

$(".tab2").click(function() {
    let parentBlock = $(this).parents(".tabs-parent2")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find(".tab2").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content2").removeClass("_active")
    $(parentBlock).find(`.tab2-${tabId}`).addClass("_active")
})

$(document).mouseup(function(e) {
    let div = $('.tabs');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).find(".tabs__select").hasClass("_active")){
            $(div).find(".tabs__select").removeClass("_active")
        }
    }
});

let fancybox = document.querySelectorAll("[data-fancybox]")

Fancybox.defaults.placeFocusBack = false

$(".rating__item").click(function() {
    let ratingItem = parseInt($(this).attr("data-rating-value"))
    $(this).parent(".rating").attr("data-total-rating", ratingItem)
})

$(".hint").click(function () {
    $(".hint").removeClass("_active")
    $(this).addClass("_active")
})
$(document).mouseup(function(e) {
    let div = $('.hint');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).hasClass("_active")){
            $(div).removeClass("_active")
        }
    }
});
